$dark: #3B4040;
$mid: #767F7F;
$light: #EDFFFF;
// main stuff
*{
	nav {
		width: 100%;
		height: 20%;
		background-color: $dark;
		text-align: center;

	}
	main {
		top: 20%;
		height: 80%;
		width: 100%;
	}

	footer {
		top: 80;
		width: 100%;
		background-color: $mid;
	}


	// misc stuff
	i {
		border: solid black;
		border-width: 0 3px 3px 0;
		display: inline-block;
		padding: 3px;
	}
	.down {
		transform: rotate(45deg);
		-webkit-transform: rotate(45deg);
	}
}
